.index-page {
  background: $primary;

  .footer {
    display: none;
  }

  .navigation {
    display: none;
  }
}

.index {
  display: flex;
  flex-direction: column;
  justify-content: center;
  position: relative;
  z-index: 1;
  flex-grow: 1;

  &__door-left,
  &__door-right {
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 50%;
    height: 100%;
    position: absolute;
    z-index: 20;
    top: 0;
    left: 0;
    background-color: $primary;
    border-right: 1px solid $gray-800;
    border-top: 2px solid $gray-800;
    transition: 3s ease-in-out;
  }

  &__door-right {
    left: 50%;
    border-right: 0;
    border-left: 1px solid $gray-800;
  }

  &__door-text {
    text-align: center;
    width: 100%;
    //background: repeating-linear-gradient(-45deg, #111 0, #111, 20px, $secondary, 20px, $secondary 40px);
    //background: repeating-linear-gradient(-45deg, #111 0, #111, 20px, darken($secondary, 10%), 20px, darken($secondary, 10%) 40px);
    background: url('../images/page-bg.jpg');
    border-top: 2px solid $gray-800;
    border-bottom: 2px solid $gray-800;
    font-size: 24px;
    font-weight: 700;
    line-height: 1.2;
    padding: 18px 0;
    color: $white;
    text-transform: uppercase;
    text-shadow:
      2px 2px #000,
      2px -2px #000,
      -2px 2px #000,
      -2px -2px #000,
      0 2px #000,
      2px 0 #000,
      -2px 0 #000,
      0 -2px #000,
      0 3px 15px rgba(#000, 0.8);
  }

  &__door-right &__door-text {
    //background: repeating-linear-gradient(135deg, #111 0, #111, 20px, $secondary, 20px, $secondary 40px);
    background: url('../images/page-bg.jpg');
  }

  &:hover &__door-left {
    left: -50%;
  }

  &:hover &__door-right {
    left: 100%;
  }

  @media (max-width: 1199px) {
    &__door-text {
      font-size: 30px;
    }
  }

  @media (max-width: 991px) {
    &__door-text {
      font-size: 24px;
    }
  }

  @media (max-width: 767px) {
    &__door-text {
      font-size: 20px;
    }
  }

  @media (max-width: 575px) {
    &__door-text {
      font-size: 13px;
      text-shadow:
        1px 1px #000,
        1px -1px #000,
        -1px 1px #000,
        -1px -1px #000,
        0 1px #000,
        1px 0 #000,
        -1px 0 #000,
        0 -1px #000,
        0 3px 15px rgba(#000, 0.8);
    }
  }
}

.bee {
  display: block;
  width: 300px;
  max-width: 100%;
  margin: 0 auto;

  &__hex {
    display: block;
    position: relative;
    overflow: hidden;
    transform: rotateZ(90deg);
    filter: drop-shadow(0 1px 4px rgba(#000, 0.2));
    transition: .4s;

    &::before {
      content: '';
      display: block;
      width: 100%;
      padding-bottom: 86.6%;
      filter: drop-shadow(0 1px 8px rgba(#000, 0.3));
    }
  }

  &__xa {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    transform: rotateZ(120deg);
    overflow: hidden;
  }

  &__gon {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    transform: rotateZ(120deg);
    overflow: hidden;
    background-color: #25A60F;
    transition: .4s;

    img {
      position: absolute;
      top: 50%;
      left: 50%;
      width: 80%;
      height: 80%;
      object-fit: contain;
      object-position: 50% 50%;
      transform: translate(-50%, -50%) rotateZ(30deg);
      transform-origin: 50% 50%;
    }
  }

  &:hover &__gon {
    background-color: $white;
  }

  @media (max-width: 991px) {
    width: 200px;
  }
}

.index-welcome {
  font-size: 30px;
  font-weight: 700;
  text-transform: uppercase;
  color: $white;
  margin-top: 60px;
  text-align: center;
  width: 100%;
  padding: 0 8px;

  @media (max-width: 991px) {
    font-size: 20px;
    margin-top: 22px;
  }
}
