.section {
  &__title {
    font-size: 30px;
    font-weight: 700;
    text-transform: uppercase;
    margin-bottom: 12px;
  }

  &__desc {
    margin-bottom: 20px;
  }
}

.section-2 {
  padding: 60px 0 30px;
  position: relative;
  z-index: 1;

  &__bg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: 50% 50%;
    z-index: -10;
  }

  &__subtitle {
    font-family: $yen-tu;
    font-size: 40px;
    line-height: 1;
    text-align: center;
    margin-bottom: 8px;
  }

  &__title {
    font-size: 30px;
    font-weight: 700;
    text-transform: uppercase;
    line-height: 1.2;
    text-align: center;
    margin-bottom: 40px;
  }

  &--white &__subtitle,
  &--white &__title {
    color: $white;
  }
}

.section-3 {
  padding: 45px 0 15px;

  &__desc {
    text-align: center;
    margin-bottom: 40px;

    a {
      color: inherit;
    }
  }
}

.aside {
  &:not(:last-child) {
    margin-bottom: 30px;
  }

  &__title {
    font-size: 18px;
    font-weight: 700;
    text-transform: uppercase;
    color: $white;
    text-align: center;
    padding: 5px;
    border-radius: 20px;
    background-color: $primary;
    margin-bottom: 12px;
  }
}

.quality-section {
  position: relative;
  z-index: 1;
  overflow: hidden;

  &__img-1 {
    position: absolute;
    z-index: -1;
    left: 0;
    top: 5%;
  }

  &__img-2 {
    position: absolute;
    right: 0;
    bottom: 5%;
    z-index: -1;
  }

  @media (max-width: 767px) {
    &__img-1,
    &__img-2 {
      display: none;
    }
  }
}

.product-section {
  position: relative;
  z-index: 1;
  overflow: hidden;

  &__img-1 {
    position: absolute;
    z-index: -1;
    left: 0;
    bottom: 5%;
  }

  &__img-2 {
    position: absolute;
    right: 0;
    top: 5%;
    z-index: -1;
  }

  @media (max-width: 767px) {
    &__img-1,
    &__img-2 {
      display: none;
    }
  }
}

.pros-section {
  position: relative;
  z-index: 1;
  overflow: hidden;

  &__img-1 {
    position: absolute;
    z-index: -1;
    top: 50%;
    left: 0;
    transform: translateY(-50%);
  }

  &__img-2 {
    position: absolute;
    z-index: -1;
    top: 50%;
    right: 0;
    transform: translateY(-50%);
  }

  @media (max-width: 1199px) {
    &__img-1,
    &__img-2 {
      display: none;
    }
  }
}

.gallery-section {
  position: relative;
  z-index: 1;

  &__img {
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
  }

  @media (max-width: 1199px) {
    &__img {
      display: none;
    }
  }
}
