.contact-map {
  height: 100%;
  min-height: 250px;
}

.contact-form {
  &__title {
    font-size: 30px;
    font-weight: 700;
    text-transform: uppercase;
    margin-bottom: 8px;
  }

  &__text {
    margin-bottom: 16px;
  }

  .form-control {
    border: 0;
    border-radius: 20px;
    padding: 8px 20px;
  }

  input.form-control {
    height: 40px;
  }

  @media (max-width: 767px) {
    &__title {
      font-size: 24px;
    }
  }
}
