.home-page {
  background: $primary;

  .footer {
    background-color: $white;
    border-top: 0;
  }

  .search {
    @media (min-width: 1200px) {
      .form-control {
        background: 0;
        border-color: $border-color;
        color: $white;

        &::placeholder {
          color: $white;
        }
      }

      .input-group-text {
        background: 0;
        border-color: $border-color;
        color: $white;
      }
    }
  }

  .navigation {
    background-color: $primary;
    border-top: 0;
    border-bottom-color: $white;

    &__btn {
      color: $white;
    }
  }

  .header__img {
    @media (min-width: 1200px) {
      display: block;
      height: 70px;
      position: absolute;
      top: 50%;
      right: 0;
      transform: translateY(-50%);
    }
  }

  .menu-root > .menu-item > .menu-link {
    @media (min-width: 1200px) {
      color: $white;

      &:hover {
        color: $white;
      }

      &::after {
        border-bottom-color: $white;
      }
    }
  }
}
