.pagination {
  margin-bottom: 0;

  .page-item {
    &:not(:last-child) {
      margin-right: 8px;
    }
  }

  .page-link {
    color: $gray-800;
    padding: 8px 16px;
    border-radius: 4px;
    border: 0 !important;
  }
}

.breadcrumb {
  margin-bottom: 20px;
  font-weight: 700;

  &-item {
    &,
    & + &::before,
    &.active,
    &.active::before,
    a,
    a:hover {
      color: $gray-800;
    }
  }
}

$phoneRingColor: #e73a35;
.phone-ring {
  display: flex;
  width: 40px;
  height: 40px;
  justify-content: center;
  align-items: center;
  color: #fff;
  border-radius: 50%;
  background-color: $phoneRingColor;
  position: fixed;
  z-index: 100;
  bottom: 50px;
  left: 50px;
  box-shadow: 0 5px 20px rgba(#000, 0.3);

  &:hover {
    color: $white;
  }

  &::before {
    content: "";
    display: block;
    width: 250%;
    height: 250%;
    border-radius: 50%;
    border: 1px solid $phoneRingColor;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    animation: phone-circle-animate 1s ease-in-out infinite;
  }

  &::after {
    content: "";
    display: block;
    width: 160%;
    height: 160%;
    border-radius: 50%;
    position: absolute;
    left: 50%;
    top: 50%;
    z-index: -1;
    transform: translate(-50%, -50%);
    background-color: rgba($phoneRingColor, 0.5);
    animation: phone-circle-fill-animate 2s ease-in-out infinite;
  }

  &__icon {
    display: flex;
    width: 100%;
    height: 100%;
    justify-content: center;
    align-items: center;
    font-size: 20px;
    background-color: rgba($phoneRingColor, 0.7);
    border-radius: 50%;
    position: absolute;
    top: 0;
    left: 0;
    animation: phone-icon-animate 1s ease-in-out infinite;
  }

  &__text {
    display: inline-block;
    background-color: rgba($phoneRingColor, 0.7);
    font-size: 14px;
    font-weight: 700;
    line-height: 40px;
    padding: 0 20px 0 60px;
    color: $white;
    border-radius: 20px;
    position: absolute;
    z-index: -1;
    top: 0;
    left: 0;
    box-shadow: 0 5px 20px rgba(#000, 0.3);
  }

  &:hover &__text {
    color: $phoneRingColor;
    background-color: rgba(#fff, 0.7);
  }

  @media (max-width: 767px) {
    left: 20px;
    bottom: 20px;

    &__text {
      display: none;
    }
  }
}

.as-menu {
  list-style: none;
  margin: 0;
  padding: 0;

  &__item {
    &:not(:last-child) {
      border-bottom: 1px dashed $secondary;
      padding-bottom: 10px;
      margin-bottom: 10px;
    }
  }

  &__link {
    display: block;
    font-size: 14px;
    font-weight: 700;
    color: $gray-800;
    padding: 0 15px;

    &.active,
    &:hover {
      color: $primary;
      text-decoration: none;
    }
  }
}

.banner {
  display: flex;
  flex-direction: column;
  justify-content: center;
  position: relative;
  overflow: hidden;
  z-index: 1;
  height: 350px;

  &__bg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: 50% 50%;
    z-index: -1;
  }

  &__title {
    font-size: 30px;
    font-weight: 700;
    text-transform: uppercase;
    text-align: center;
    color: $primary;
    line-height: 1.2;
  }

  &__breadcrumb {
    display: flex;
    justify-content: center;

    .breadcrumb {
      margin-bottom: 0;
      padding: 8px 0;

      //.breadcrumb-item,
      //.breadcrumb-item + .breadcrumb-item::before,
      //a,
      //a:hover {
      //  color: $primary;
      //}
    }
  }

  @media (max-width: 767px) {
    height: 200px;

    &__title {
      font-size: 24px;
    }
  }
}

.forum-form {
  .form-control {
    border: 0;
    padding: 8px 20px;
    border-radius: 20px;
  }

  input.form-control {
    height: 40px;
  }
}

.comments {
  border-top: 1px dashed $secondary;
  margin-top: 24px;
  padding-top: 24px;
  margin-bottom: 24px;
}

.comment {
  & + & {
    margin-top: 24px;
  }

  & + &--reply {
    margin-top: 4px;
  }

  &--reply + &--reply {
    margin-top: 24px;
  }

  &__avatar {
    @include frame('circle');
    width: 85px;
    margin-right: 20px;
    border: 1px solid $primary;
  }

  &__header {
    display: flex;
    align-items: baseline;
    justify-content: space-between;
    margin-bottom: 12px;
  }

  &__name {
    font-size: 16px;
    font-weight: 700;
  }

  &__time {
    color: #999;
    margin-left: 12px;
  }

  &__body {
    text-align: justify;
  }

  &__footer {
    display: flex;
    justify-content: flex-end;
    margin-top: 10px;
  }

  &__reply {
    font-size: 14px;
    font-weight: 700;
    color: $gray-800;
  }

  &--reply {
    background-color: $white;
    margin-left: 105px;
    border-radius: 30px;
    padding: 12px;
    position: relative;

    &::before {
      content: '';
      border-style: solid;
      border-width: 20px;
      border-color: transparent transparent $white;
      position: absolute;
      bottom: 100%;
      left: 30px;
    }
  }

  @media (max-width: 767px) {
    &__avatar {
      width: 60px;
      margin-right: 10px;
    }

    &__header {
      flex-direction: column;
      margin-bottom: 4px;
    }

    &__name {
      font-size: 14px;
    }

    &__time {
      margin-left: 0;
    }

    &--reply {
      margin-left: 70px;
    }
  }
}

.pd-info {
  &__title {
    font-size: 30px;
    line-height: 1.2;
    font-weight: 700;
    margin-bottom: 16px;
  }

  &__social {
    margin-bottom: 16px;
  }

  &__desc {
    margin-bottom: 16px;
  }
}

.detail-tabs {
  .nav-item {
  }

  .nav-link {
    font-size: 20px;
    font-weight: 700;
    text-transform: uppercase;
    padding: 5px 45px;
    border-radius: 20px 20px 0 0;
    color: $gray-800;

    &.active {
      background-color: $primary;
      color: $white;
    }
  }

  @media (max-width: 767px) {
    .nav-link {
      padding: 8px 20px;
      border-radius: 10px 10px 0 0;
      font-size: 16px;
    }
  }
}

.detail-tab-content {
  padding: 16px;
  border: 1px solid $secondary;
  border-radius: 0 10px 10px 10px;
}

.welcome {
  padding: 50px 0 20px;

  &__subtitle {
    font-family: $yen-tu;
    font-size: 40px;
    line-height: 1;
  }

  &__title {
    font-size: 30px;
    font-weight: 700;
    text-transform: uppercase;
    line-height: 1.2;
  }
}

.intro {
  position: relative;
  z-index: 2;
  padding: 80px 0;

  &__bg {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: 50% 50%;
    position: absolute;
    z-index: -3;
    top: 0;
    left: 0;
  }

  &__img {
    position: absolute;
    bottom: 0;
    right: 5%;
    transform: translateY(20%);
    z-index: -1;
  }

  &__title {
    font-size: 50px;
    font-weight: 700;
    text-align: center;
    color: $white;
    text-shadow: 0 1px 6px rgba($secondary, 0.6);
    margin-bottom: 30px;
  }

  &__desc {
    font-size: 16px;
    font-weight: 700;
    text-align: center;
    color: $white;
    text-shadow: 0 1px 6px rgba($secondary, 0.6);
    max-width: 900px;
    margin: 0 auto;
  }

  @media (max-width: 1199px) {
    &__img {
      display: none;
    }
  }
}

.pros {
  &__icon {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 80px;

    img {
      max-height: 100%;
      object-fit: contain;
      object-position: 50% 50%;
    }
  }

  &__name {
    font-size: 18px;
    font-weight: 700;
    text-align: center;
    margin: 12px 0 14px;
  }

  &__desc {
    text-align: center;
    max-width: 250px;
    margin: 0 auto;
  }
}

.testimonial {
  &__text {
    text-align: center;
    color: $white;
    max-width: 800px;
    margin: 0 auto;
    font-style: italic;
  }

  &__name {
    text-align: center;
    color: $white;
    font-weight: 700;
    margin-bottom: 6px;
  }

  &__pos {
    text-align: center;
    color: $white;
  }

  &__icon {
    text-align: center;
    color: $white;
    font-size: 50px;
    line-height: 1;
    margin: 10px 0;
  }
}

.home-section {
  padding: 30px 0 0;

  &__content {
    font-size: 16px;
    color: $white;
    font-weight: 700;
    margin-bottom: 30px;
  }
}
