.news {
  &__frame {
    @include frame('rect', 56.25%);
    @include frame('zoom');
    margin-bottom: 8px;
  }

  &__info {
    color: #999;
    text-align: center;
    margin-bottom: 8px;
  }

  &__title {
    font-size: 16px;
    font-weight: 700;
    text-align: center;
  }

  @media (max-width: 575px) {
    &__info {
      font-size: 12px;
    }

    &__title {
      font-size: 14px;
    }
  }
}

.news-2 {
  &__frame {
    @include frame('rect', 56.25%);
    @include frame('zoom');
    width: 125px;
    margin-right: 8px;
  }

  &__title {
    font-size: 15px;
    font-weight: 700;
    line-height: 18px;
    margin-top: -2px;
    margin-bottom: 0;
  }
}

.product {
  padding-top: 10%;

  &__frame-1 {
    display: block;
    position: relative;
    overflow: hidden;
    transform: rotateZ(90deg);
    filter: drop-shadow(0 1px 4px rgba($primary, 0.2));
    transition: .4s;

    &::before {
      content: '';
      display: block;
      width: 100%;
      padding-bottom: 86.6%;
      filter: drop-shadow(0 1px 8px rgba($primary, 0.3));
    }
  }

  &:hover &__frame-1 {
    transform: rotateZ(90deg) scale(1.05);
  }

  &__frame-2 {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    transform: rotateZ(120deg);
    overflow: hidden;
  }

  &__frame-3 {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    transform: rotateZ(120deg);
    overflow: hidden;
    background-color: $white;

    img {
      position: absolute;
      top: 50%;
      left: 50%;
      width: 90%;
      height: 90%;
      object-fit: cover;
      object-position: 50% 50%;
      transform: translate(-50%, -50%) rotateZ(30deg);
      transform-origin: 50% 50%;
    }
  }

  &__title {
    font-size: 14px;
    font-weight: 700;
    color: $white;
    background-color: $primary;
    text-align: center;
    padding: 4px;
    margin-bottom: 0;
    margin-top: 10%;

    a:hover {
      color: $white;
    }
  }
}

.facility {
  @include frame('rect', 56.25%);
  @include frame('shine');
  border: 8px solid $white;

  @media (max-width: 767px) {
    border-width: 4px;
  }
}

.gallery {
  &__frame {
    @include frame('rect', 56.25%);
    @include frame('shine');
  }
}
