.btn-default {
  border-color: $border-color;
  background-color: transparent;

  &:hover {
    border-color: $primary;
    color: $primary;
  }
}

.btn-movetop {
  display: none;
  position: fixed;
  z-index: $zindex-sticky;
  bottom: 30px;
  right: 30px;
}

$rounded-list: 8 10 18 20 22 24 25 26 28 30;
@each $item in $font-size-list {
  .rounded-#{$item} {
    border-radius: #{$item}px;
  }
}

.button {
  @include btn('filter');
  display: inline-block;
  font-size: 20px;
  font-weight: 700;
  color: $white;
  line-height: 40px;
  padding: 0 30px;
  border-radius: 20px;
  border: 0;
  background: $secondary;

  &:hover {
    color: $white;
    text-decoration: none;
  }

  &--light {
    text-transform: uppercase;
    font-weight: 400;
    letter-spacing: 1.5px;
  }

  @media (max-width: 767px) {
    line-height: 35px;
    padding: 0 20px;
    border-radius: 18px;
    font-size: 14px;
  }
}
