.header {
  background-color: $white;
  position: relative;
  z-index: 100;

  &__inner {
    display: flex;
    align-items: center;
    padding: 16px 0;
    position: relative;
    justify-content: center;
  }

  &__logo {
    display: inline-flex;

    img {
      height: 80px;
    }
  }

  &__img {
    display: none;
  }

  @media (min-width: 1200px) {
    &__inner {
      padding: 16px 0 30px;
    }

    &__logo {
      img {
        height: 135px;
      }
    }
    &__btn {
      display: none;
    }
  }
}

.navigation {
  border-top: 2px solid $primary;
  border-bottom: 5px solid $primary;

  &__inner {
    display: flex;
    align-items: center;
    position: relative;
  }

  &__btn {
    display: inline-flex;
    height: 48px;
    background: 0;
    border: 0;
    color: $primary;
    padding: 0 ;
    font-size: 24px;
    justify-content: center;
    align-items: center;
    margin-right: auto;
  }

  @media (min-width: 1200px) {
    &__btn {
      display: none;
    }
  }
}

.search {
  padding: 10px 20px 20px;

  .input-group-text {
    background-color: $white;
    border-radius:  0 15px 15px 0;
    border: 1px solid $primary;
    color: $primary;
    border-left: 0;
  }

  .form-control {
    border: 1px solid $primary;
    border-right: 0;
    border-radius: 15px 0 0 15px;
    padding: 0 16px;
    height: 30px;
    font-size: 12px;
    color: $primary;
    font-style: italic;

    &::placeholder {
      color: $primary;
    }
  }

  @media (min-width: 1200px) {
    width: 280px;
    position: absolute;
    top: 50%;
    right: 0;
    transform: translateY(-50%);
    padding: 0;
  }
}

.lang {
  display: flex;

  &__item {
    display: flex;

    &:not(:last-child) {
      margin-right: 10px;
    }

    img {
      height: 18px;
    }
  }

  @media (min-width: 1200px) {
    order: 1;
    margin-right: 300px;
    margin-left: 20px;
  }
}

