.preview {
  &__thumbs {
    margin-top: 16px;
  }

  @media (max-width: 575px) {
    &__thumbs {
      margin-top: 8px;
    }
  }
}

.preview-slider {
  &__frame {
    @include frame('rect', 75%);
    background-color: $white;
    border: 1px solid $secondary;
    border-radius: 10px;
  }
}

.thumb-slider {
  position: relative;
  max-width: 250px;
  margin: 0 auto;

  &__frame {
    @include frame('rect', 75%);
    border-radius: 4px;
    border: 1px solid $secondary;
  }

  &.has-nav {
    padding: 0 20px;
  }

  &.has-nav &__prev,
  &.has-nav &__next {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 20px;
    height: 100%;
    cursor: pointer;
    position: absolute;
    top: 0;
    left: 0;
    font-size: 20px;
    color: $secondary;

    &:focus {
      outline: 0;
    }

    &.swiper-button-disabled {
      display: none;
    }
  }

  &.has-nav &__next {
    left: auto;
    right: 0;
  }

  &:not(.has-nav) &__prev,
  &:not(.has-nav) &__next {
    display: none;
  }
}
