.testimonial-slider {
  position: relative;
  z-index: 1;
  padding-bottom: 30px;

  &__pagination {
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    z-index: 10;

    .swiper-pagination-bullet {
      width: 14px;
      height: 14px;
      background: 0;
      border: 1px solid $white;
      opacity: 1;
      margin: 0 4px;

      &:focus {
        outline: 0;
      }

      &-active {
        background: $white;
      }
    }
  }
}
