.post {
  &-title {
    font-size: 30px;
    font-weight: 700;
    margin-bottom: 10px;
    line-height: 1.2;
  }

  &-info {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    color: #999;
    margin-bottom: 10px;
  }

  &-sapo {
    font-size: 14px;
    font-weight: 700;
    margin-bottom: 16px;
  }
}

.relate-post {
  &__title {
    font-size: 30px;
    font-weight: 700;
    margin-bottom: 8px;
  }

  &__list {
    list-style: none;
    margin: 0;
    padding: 0;
  }

  &__item {
    &:not(:last-child) {
      margin-bottom: 8px;
    }
  }

  &__link {
    display: inline-block;
    padding-left: 22px;
    position: relative;
    color: $gray-800;
    font-weight: 700;

    &::before {
      content: '';
      display: block;
      width: 10px;
      height: 10px;
      border-radius: 50%;
      background-color: $gray-800;
      position: absolute;
      top: 4px;
      left: 0;
      font-size: 8px;
      color: $primary;
    }
  }
}
